import styled from "styled-components";
import {mainTheme} from "/theme/theme";

export const OurProducts = styled(`div`)`
  width: 1200px;
  margin: 0 auto;
  margin-bottom: 75px;
`;

export const OurProducts__Title = styled(`h3`)`
  font-weight: bold;
  font-size: 32px;
  line-height: 28px;
  color: ${mainTheme.colorsList.darkGreyColor};
  margin-bottom: 42px;
`;

export const OurProducts__Content = styled(`div`)`
  padding-top: 32px;
`;
