import styled from "styled-components";
import {mainTheme} from "/theme/theme";

export const Tabs = styled(`div`)`
  display: flex;
  align-items: center;
  justify-content: flex-start;
`;

type TabProps = {
  active: boolean;
};

export const Tab = styled(`div`)<TabProps>`
  position: relative;
  font-size: 22px;
  line-height: 28px;
  color: ${mainTheme.colorsList.greyColor1};
  margin: 0px 33px;
  cursor: pointer;
  transition: color .3s;
    
  &:hover {
    color: ${mainTheme.colorsList.darkGreyColor};
  }
  
  &:first-child {
    margin-left: 0;
  }
  
  &:before {
    content:"";
    position: absolute;
    height: 2px;
    top: 100%;
    left: 0;
    width: 0%;
    background: ${mainTheme.colorsList.greenColor3};
    transition: width .3s;
  };
  
  ${({active}) => active && `
    color: ${mainTheme.colorsList.darkGreyColor};
    
    &:before {
      width: 100%;
    }
  `}
`;
