import styled from "styled-components";
import {mainTheme} from "/theme/theme";

export const MainThemesList = styled(`div`)`
  position: relative;
  width: 100%;
`;

export const MainThemesList__Content = styled(`div`)`
  display: flex;
  flex-direction: column;
   max-width: 1200px;
  margin: 0 auto;

`;

export const MainThemesList__Background = styled(`div`)`
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100vw;
  height: 100%;
  z-index: -1;
  overflow-y: none;
`;
