import styled from "styled-components";
import {mainTheme} from "../../../theme/theme";

export const MainBanner = styled(`div`)`
  position: relative;
  //background: ${mainTheme.colorsList.greenColor2};
  width: 100%;
  margin-bottom: 54px;
`;

export const MainBanner__Content = styled(`div`)`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  min-height: 665px;
  max-width: 1200px;
  margin: 0 auto;
`;

export const MainBanner__Title = styled(`h1`)`
  font-weight: bold;
  font-size: 64px;
  line-height: 84px;
  color: #FFFFFF;
  max-width: 466px;
  margin-bottom: 27px;
`;

export const MainBanner__Description = styled(`h3`)`
  font-weight: normal;
  font-size: 20px;
  line-height: 28px;
  max-width: 544px;
  color: #fff;
  margin-bottom: 53px;
`;

export const MainBanner__CatalogLink = styled(`a`)`
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 19px 71px;
  width: 321px;
  height: 60px;
  background: #37B97D;
  box-shadow: ${mainTheme.boxShadow.buttonShadow};
  border-radius: ${mainTheme.borderRadius.buttonRadius};
  color: #fff;
  transition: box-shadow .3s, transform .3s, color .3s;
  
  &:hover {
    color: ${mainTheme.colorsList.greenColor4};
    box-shadow: ${mainTheme.boxShadow.buttonShadowHover};
    transform: scale(1.02);
  }
`;

export const MainBanner__ImageWrap = styled(`div`)`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  img {
    width: 100%;
    object-fit: cover;
  }
`;
