import React, {FC, useEffect, useState} from "react";
import * as S from "./OurProducts.styles";

// Components
import {Tabs, TabTypes} from "../Tabs/Tabs";
import {CatalogItem, CatalogItemProps} from "../CatalogItem/CatalogItem";
import {Row, Col} from "antd";
import {idGenerator} from "../../../lib/idGenerator";


export type OurProductsProps = {
    tabsList: TabTypes<string>[];
    items: Record<string, CatalogItemProps[]>;
};

export const OurProducts: FC<OurProductsProps> = ({tabsList, items}) => {
    const [productType, setProductType] = useState<string | undefined>(null);

    const onClickHandler = (value: string) => {
        if (value === productType) return;
        setProductType(value);
    }

    useEffect(() => {
        if (tabsList && tabsList.length) {
            setProductType(tabsList[0].value);
        }
    }, [tabsList])

    return (
        <S.OurProducts>
            <S.OurProducts__Title>
                Наши товары
            </S.OurProducts__Title>
            <Tabs items={tabsList}
                  onClickHandler={onClickHandler}
                  activeValue={productType}
            />
            {
                items && productType &&
                <S.OurProducts__Content>
                    <Row gutter={[56, 67]}>
                        {
                            items[productType].map((item) => (
                                <Col span={6}>
                                    <CatalogItem {...item} key={idGenerator()} />
                                </Col>
                            ))
                        }
                    </Row>
                </S.OurProducts__Content>
            }

            {productType}
        </S.OurProducts>
    );
}