import styled from "styled-components";
import { mainTheme} from "../../../../../theme/theme";

export const PageList = styled(`div`)`
  display: flex;
  align-items: center;
`;

export const PageLinkWrap = styled(`div`)`
  margin-right: 3.2rem;
  transition: transform .3s, color .3s;
  &:hover {
    transform: scale(1.1);
  }
`;

export const PageLink = styled(`a`)`
  font-weight: 600;
  font-size: 14px;
  line-height: 22px;
  color: #292929;
  transition: transform .3s, color .3s;
  
  &:hover {
    color: ${mainTheme.colorsList.greenColor3};
    
  }
`;


