import React from "react";
import * as S from "./Header.styles";

// Components
import { Select, Button, Tooltip } from "antd";
// @ts-ignore
import FaduLogo from "../../../public/images/FaduLogo.png";
// @ts-ignore
import DeliveryIcon from "../../../public/svg/deliveryIcon.svg";
// @ts-ignore
import CartIcon from "../../../public/svg/cartIcon.svg";
import {PageList} from "./components/PageList/PageList";
import {pageList} from "../../../mock/PageList";
import {mainTheme} from "/theme/theme";

const { Option } = Select;

export const Header = ({}) => (
  <S.Header>
    <S.Header__Content>
        <S.Header__LangSelect defaultValue="ru" bordered={false}>
            <Option value="ru">Rus</Option>
            <Option value="eng">Eng</Option>
        </S.Header__LangSelect>
        <S.Header__LogoWrap>
            <img src={FaduLogo} />
        </S.Header__LogoWrap>
        <PageList items={pageList}/>
        <S.Header__ButtonPanel>
            <Tooltip placement="bottom"
                     title="Доставка"
                     color={mainTheme.colorsList.greenColor1}>
                <S.Header__Button>
                    <img src={DeliveryIcon} />
                </S.Header__Button>
            </Tooltip>
            <Tooltip placement="bottom" title="В Корзину" color={mainTheme.colorsList.greenColor1}>
                <S.Header__Button>
                    <img src={CartIcon} />
                    <S.Header__CartCount>
                        <span>
                            1
                        </span>
                    </S.Header__CartCount>
                </S.Header__Button>
            </Tooltip>
        </S.Header__ButtonPanel>
    </S.Header__Content>
  </S.Header>
);