import React, {FC, useState} from "react";
import * as S from "./CatalogItem.styles";
import {priceFormater} from "../../../lib/priceFormater";

export type CatalogItemProps = {
    IMAGE: string;
    HOVER_IMAGE?: string;
    NAME: string;
    DESCRIPTION?: string;
    TONE?: string;
    WEIGHT?: number;
    PRICE: number;
};

export const CatalogItem: FC<CatalogItemProps> = ({
  IMAGE,
  NAME,
                                                      HOVER_IMAGE,
  DESCRIPTION,
  TONE,
  WEIGHT,
                                                      PRICE
}) => {
    const [hoverMode, setHoverMode] = useState<boolean | undefined>(null);
    return (
        <S.CatalogItem onMouseEnter={() => setHoverMode(true)}
                       onMouseLeave={() => setHoverMode(false)}
        >
            <S.CatalogItem__Content>
                <S.CatalogItem__ImageWrap isHover={hoverMode && HOVER_IMAGE}>
                    <img src={IMAGE}/>
                    <img src={HOVER_IMAGE ? HOVER_IMAGE : ``} className="hoverImage"/>
                    <div className="blurEffect" />
                </S.CatalogItem__ImageWrap>
                <S.CatalogItem__Title>
                    {NAME}
                </S.CatalogItem__Title>
                <S.CatalogItem__Description>
                    <span>{DESCRIPTION}</span>
                </S.CatalogItem__Description>
                <S.CatalogItem__Info>
                    <span>{TONE}</span>
                    {
                        WEIGHT && "|"
                    }
                    <span>{WEIGHT}</span>
                </S.CatalogItem__Info>
                <S.CatalogItem__BottomPanel>
                    <S.CatalogItem__Price>
                    <span>
                        {priceFormater.format(PRICE) + "₽"}
                    </span>
                    </S.CatalogItem__Price>
                    <S.CatalogItem__Button>
                    <span>
                        Купить
                    </span>
                    </S.CatalogItem__Button>
                </S.CatalogItem__BottomPanel>
            </S.CatalogItem__Content>
        </S.CatalogItem>
    );
}