import React, {FC} from "react";
import * as S from "./MainBanner.styles";
import {MainBanner__CatalogLink} from "./MainBanner.styles";

export type MainBannerProps = {
    title: string;
    description?: string;
    buttonText: string;
    image?: string;
};

export const MainBanner: FC<MainBannerProps> = ({title,description, buttonText, image }) => (
    <S.MainBanner>
        <S.MainBanner__Content>
            <S.MainBanner__Title>
                {title}
            </S.MainBanner__Title>
            <S.MainBanner__Description>
                {description}
            </S.MainBanner__Description>
            <S.MainBanner__CatalogLink>
                {buttonText}
            </S.MainBanner__CatalogLink>
        </S.MainBanner__Content>
        {
            image &&
            <S.MainBanner__ImageWrap>
                <img src={image} width={1440} height={665}/>
            </S.MainBanner__ImageWrap>
        }

    </S.MainBanner>
);
