import styled from "styled-components";
import {mainTheme} from "/theme/theme";
import {MainThemeEllipseProps} from "./MainThemeEllipse";

export const MainThemeEllipse = styled(`div`)<MainThemeEllipseProps>`
  position: absolute;
  border-radius: 50%;
  
  ${({
       circleWidth,
       circleHeight, 
       top, 
       bottom, 
       left, 
       right, 
       color, 
       deg, 
       borderWidth,
       borderStyle
  }) => (
      `
        width: ${circleWidth ? circleWidth : 50}px;
        height: ${circleHeight ? circleHeight : 50}px;
        top: ${top ? `${top}px` : `auto`};
        bottom: ${bottom ? `${bottom}px` : `auto`};
        left: ${left ? `${left}px` : `auto`};
        right: ${right ? `${right}px` : `auto`};
        border-color: ${color ? `${color}` : `#000`};
        border-width: ${borderWidth ? borderWidth : `1`}px;
        border-style: ${borderStyle ? borderStyle : `solid`};
        transform: ${deg ? `rotate(${deg}deg)` : `rotate(0)`};
      `
  )}
`;
