import React, {FC} from "react";
import * as S from "./MainThemeEllipse.styles";

export type MainThemeEllipseProps = {
    circleWidth?: number;
    circleHeight?: number;
    top?: number;
    bottom?: number;
    left?: number;
    right?: number;
    color?: string;
    deg?: number;
    borderWidth?: number;
    borderStyle?: string;
};

export const MainThemeEllipse: FC<MainThemeEllipseProps> = (props) => (
    <S.MainThemeEllipse {...props}>
        {
            (props.children) ? props.children : ``
        }
    </S.MainThemeEllipse>
);