import React, {FC} from "react";
import * as S from "./PageList.styles";
import {idGenerator} from "../../../../../lib/idGenerator";

export type PageListProps = {
    items: PageLinkProps[];
};

type PageLinkProps = {
    title: string;
    href: string;
}

export const PageList:FC<PageListProps> = ({items}) => (
    <S.PageList>
        {
            items.map(item => (
                <S.PageLinkWrap id={idGenerator()}>
                    <S.PageLink href={item.href}>
                        {item.title}
                    </S.PageLink>
                </S.PageLinkWrap>
            ))
        }
    </S.PageList>
);
