import styled, {keyframes} from "styled-components";
import {mainTheme} from "/theme/theme";
import {Button} from "antd";
import is from "styled-is";

export const itemShow = keyframes`
  from {
    opacity: 0;
    transform: scale(.95);
  }
  
  to {
    opacity: 1;
    transform: scale(1);
  }
`;

export const itemShow2 = keyframes`
  from {
    opacity: 0;
  }
  
  to {
    opacity: 1;
  }
`;

export const CatalogItem = styled(`div`)`
  animation: ${itemShow} .3s linear forwards;
`;

export const CatalogItem__Content = styled(`div`)`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
`;

export type CatalogItem__ImageWrap = {
  isHover: boolean;
};

export const CatalogItem__ImageWrap = styled(`div`)<CatalogItem__ImageWrap>`
  position: relative;
  width: 257px;
  height: 290px;
  margin-bottom: 27px;
  cursor: pointer;
  img {
    width: 100%;
    height: 100%;
    border-radius: 0 30px;
    object-fit: cover;
  }
  
  .hoverImage {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    opacity: 0;
    transition: opacity .3s;
    z-index: 1;
    
    ${
        is(`isHover`)`
            opacity: 1;
        `
    }
  }
`;

export const CatalogItem__Title = styled(`h4`)`
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: ${mainTheme.colorsList.darkGreyColor};
  margin-bottom: 4px;
`;

export const CatalogItem__Description = styled(`div`)`
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  color: ${mainTheme.colorsList.darkGreyColor};
  margin-bottom: 10px;
`;

export const CatalogItem__Info = styled(`div`)`
  display: flex;
  align-items: center;
  width: fit-content;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  color: ${mainTheme.colorsList.greyColor1};
  margin-bottom: 16px;
  
  span:first-child {
    margin-right: 9px;
  }
  
  span:last-child {
    margin-left: 9px;
  }
`;

export const CatalogItem__BottomPanel = styled(`div`)`
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
`;

export const CatalogItem__Button = styled(`button`)`
  padding: 8px 46px;
  background: ${mainTheme.colorsList.greenColor3};
  color: #fff;
  border-radius: 30px;
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  border: none;
  cursor: pointer;
  transition: background .3s, transform .3s;
  
  &:hover {
    background: ${mainTheme.colorsList.greenColor1};
    transform: scale(1.05);
  }
`;

export const CatalogItem__Price = styled(`div`)`
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: ${mainTheme.colorsList.darkGreyColor};
`;
