import styled from "styled-components";
import {mainTheme} from "/theme/theme";
import is from "styled-is";

export const ThemeItem = styled(`div`)`
  //height: 580px;
  position: relative;
  margin-bottom: 125px;
`;

type ThemeItem__ContentProps = {
    leftSide?: boolean;
};

export const ThemeItem__TextBlock = styled(`div`)`
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 70px 0 0 70px;
`;

export const ThemeItem__Content = styled(`div`)<ThemeItem__ContentProps>`
  display: flex;
  align-items: flex-start;
  justify-content: space-evenly;

  ${
      is(`leftSide`)`
        flex-direction: row-reverse;
     `
  }
  
  ${ThemeItem__TextBlock} {
    ${
        is(`leftSide`)`
            padding: 70px 70px 0 0;
        `
    }
  }
`;

export const ThemeItem__ImageWrap = styled(`div`)`
  width: 50%;
  height: 780px;
  
  img {
    height: 100%;
    width: 100%;
    max-width: 577px;
    object-fit: cover;
  }
`;

export const ThemeItem__Title = styled(`h2`)`
  font-weight: 800;
  font-size: 48px;
  line-height: 56px;
  color: ${mainTheme.colorsList.darkGreyColor2};
  margin-bottom: 100px;
  max-width: 585px;
  
  ${
    is(`absoluteTitle`)`
        margin-left: -300px;
        max-width: none;
    `
  }
`;

export const ThemeItem__Desciprtion = styled(`div`)`
  margin-bottom: 15px;
  font-weight: normal;
  font-size: 24px;
  line-height: 30px;
  color: ${mainTheme.colorsList.darkGreyColor};
`;

export const ThemeItem__MoreLink = styled(`a`)`
  font-weight: normal;
  font-size: 24px;
  line-height: 30px;
  color: ${mainTheme.colorsList.greenColor2} !important;
  transition: color .3s;
  
  span {
    margin-right: 10px;
    
    transition: margin-right .3s;
  }

  &:hover {
    color: ${mainTheme.colorsList.greenColor3}  !important;
    
    span {
      margin-right: 15px;
    }
  }
`;

export const ThemeItem__SecondImage = styled(`div`)`
  position: absolute;
  bottom: -80px;
  right: 40px;
  
  img {
    width: 380px;
    height: 300px;
  }
`;
