import styled from "styled-components";
import {mainTheme} from "/theme/theme";

export const Footer = styled(`div`)`
  width: 100%;
  background: ${mainTheme.colorsList.darkGreyColor};
  padding: 38px 0;
`;

export const Footer__Content = styled(`div`)`
  max-width: 1200px;
  margin: 0 auto;
  
`;

export const Footer__Title = styled(`h4`)`
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  color: #FFFFFF;
  margin-bottom: 12px;
`;

export const Footer__BlockValue = styled(`div`)`
  display: flex;
  flex-direction: column;
  font-size: 14px;
  line-height: 22px;
  color: #FFFFFF;

`;

export const FooterPageList = styled(`div`)`
  display: flex;
  flex-direction: column;
  align-items: flex-start;
`;

export const FooterPageList__Item = styled(`a`)`
  font-size: 14px;
  line-height: 28px;
  color: #FFFFFF;
`;

export const FooterSiteInfo = styled(`div`)`
  display: flex;
  flex-direction: column;
  color: #fff;
`;

export const FooterSiteInfo__PhoneNumber = styled(`div`)`
  font-weight: 600;
  font-size: 22px;
  line-height: 22px;
  margin-bottom: 12px;
`;

export const FooterSiteInfo__Mail = styled(`div`)`
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  margin-bottom: 15px;
`

export const FooterSiteInfo__SocialList = styled(`div`)`
  display: flex;
  align-items: center;
`;

export const FooterSiteInfo__SocialLink = styled(`a`)`
  margin-right: 13px;
`;
