import React, {FC} from "react";
import * as S from "./ThemeItem.styles";

// @ts-ignore
import ArrowIcon from "/assets/moreArrowSVG.svg";

export type ThemeItemProps = {
    IMAGE: string;
    TITLE: string;
    DESCRIPTION: string;
    LINK: string;
    leftSide?: boolean;
    buttonText: string;
    SECOND_IMAGE?: string;
    absoluteTitle?: boolean;
};

export const ThemeItem: FC<ThemeItemProps> = ({
  IMAGE,
  TITLE,
  DESCRIPTION,
  LINK,
  leftSide,
  buttonText,
  SECOND_IMAGE,
                                                  absoluteTitle
}) => (
    <S.ThemeItem>
        <S.ThemeItem__Content leftSide={leftSide}>
            <S.ThemeItem__ImageWrap>
                {
                    IMAGE && <img src={IMAGE} />
                }
            </S.ThemeItem__ImageWrap>
            <S.ThemeItem__TextBlock>
                <S.ThemeItem__Title absoluteTitle={absoluteTitle}>
                    {
                        TITLE
                    }
                </S.ThemeItem__Title>
                <S.ThemeItem__Desciprtion>
                    <p>
                        {
                            DESCRIPTION
                        }
                    </p>
                </S.ThemeItem__Desciprtion>
                <S.ThemeItem__MoreLink href={LINK}>
                    <span>{buttonText}</span> <img src={ArrowIcon} />
                </S.ThemeItem__MoreLink>
                {
                    SECOND_IMAGE &&
                    <S.ThemeItem__SecondImage>
                        <img src={SECOND_IMAGE} />
                    </S.ThemeItem__SecondImage>
                }
            </S.ThemeItem__TextBlock>
        </S.ThemeItem__Content>
    </S.ThemeItem>
);