import React, {FC} from "react";
import * as S from "./Tabs.styles";

//Libs
import {idGenerator} from "../../../lib/idGenerator";

export type TabsProps<T> = {
    items: TabTypes<T>[];
    onClickHandler: (T) => void;
    activeValue: T;
};

export type ValueTypes = string | number;

export const Tabs: FC<TabsProps<ValueTypes>> = ({items, activeValue, onClickHandler}) => (
    <S.Tabs>
        {items.map(item => (
            <Tab {...item}
                 key={item.title}
                 activeValue={activeValue === item.value}
                 onClickHandler={onClickHandler}
            />
        ))}
    </S.Tabs>
);

export type TabTypes<T> = {
    title: string;
    value: T;
}

export type TabProps<T> = TabTypes<T> & {
    activeValue: boolean;
    onClickHandler: TabsProps<T>["onClickHandler"];
}

export const Tab: FC<TabProps<ValueTypes>> = ({title, value, activeValue, onClickHandler}) => (
    <S.Tab active={activeValue} onClick={() => onClickHandler(value)}>
        <span>
            {title}
        </span>
    </S.Tab>
);
