import styled from "styled-components";
import { Select } from "antd";
import {mainTheme} from "../../../theme/theme";

export const Header = styled(`header`)`
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background: #fff;
   max-height: 70px;
  z-index: 10;
  box-shadow: 0 0 10px rgb(0,0,0, 0.4);
`;

export const Header__Content = styled(`div`)`
    max-width: 120rem;
    padding: 1.7rem;
    margin: 0 auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  max-height: inherit;
`;

export const Header__LangSelect = styled(Select)`
  margin-right: 3.5rem;
  .ant-select-selection-item {
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
  }
  
  .ant-select-arrow {
    color: #000;
    font-size: 10px;
    margin-top: -3px;
    font-weight: 600;
  }
`;

export const Header__LogoWrap = styled(`div`)`
  margin-right: 6.4rem;
  
  img {
    width: 102px;
    height: 40px;
  }
`;

export const Header__ButtonPanel = styled(`div`)`
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
  
`;

export const Header__Button = styled(`a`)`
  position: relative;
  padding: 0 10px;
  transition: transform .3s;
  &:hover {
    transform: scale(1.1);
  }
`;

export const Header__CartCount = styled(`div`)`
  position: absolute;
  top: -5px;
  right: -5px;
  width: 16px;
  height: 16px;
  background: ${mainTheme.colorsList.greenColor3};
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  font-size: 10px;
  line-height: 16px;
  /* identical to box height, or 160% */
  color: #FFFFFF;
`

